import React, { useContext, useEffect } from "react";
import "./Detailbook.css";
import BackspaceIcon from "@mui/icons-material/Backspace";
import Context from "../../../ContextHooks/Context";
import { useDispatch, useSelector } from "react-redux";
import { GETAPI_READBOOK } from "../../../REDUX/Actions";
import LoadingDetailBook from "../../Loading/LoadingDetailBook";
import { Container } from "@mui/system";
import { Button, Typography } from "@mui/material";

function Detailbook() {
  const Selector = useSelector((state) => state.Reducers);
  const {
    GET_API_READBOOK_RESULT,
    GET_API_READBOOK_LOADING,
  } = Selector;

  const Contexts = useContext(Context);
  const { setDetailBook, id,showBook,setShowBook } = Contexts;

  const dispatch = useDispatch();
  useEffect(() => {
    if (id) {
      dispatch(GETAPI_READBOOK(id));
    }

    if(!showBook) {
        dispatch(GETAPI_READBOOK(id))
      
     
 }
  }, [id,showBook]);

  const DataDetailBook = GET_API_READBOOK_RESULT.data
    ? GET_API_READBOOK_RESULT.data.data
    : null; 
 
 
  const HandlingRead = (data_nomor) => {
   setShowBook(false)
 }


 
 document.addEventListener('keydown',function(e){
  if(e.key === "Escape"){
    setDetailBook(false)
  }
 })

  return (
    <>
      <div id="detailBook">
        <div className="closeIcon" onClick={() => setDetailBook(false)}>
          <BackspaceIcon /> <br/><span className="textExit">
          ESC (exit)
        </span>
        </div> 
        {GET_API_READBOOK_LOADING ? (
          <LoadingDetailBook />
        ) : DataDetailBook ? (
          <>
            <div style={{  padding: ".5rem" }}>
              <Typography
                variant="h2"
                component="h2"
                sx={{ textAlign: "center", marginTop: ".5rem" }}
              >
                {DataDetailBook.nama}
              </Typography>

              <Typography
                variant="h4"
                component="h4"
                sx={{ textAlign: "center" }}
              >
                ({DataDetailBook.namaLatin})
              </Typography>
            </div>

            <Container>
              <div className="detailbookBox">
                <div className="contentHead" style={{borderBottom:"solid 1px #ccc",paddingBottom:"1rem",display:'flex',justifyContent:'space-between'}}>
                  <div className="descContent" >
                  <Typography  variant="body1" sx={{color:"#006064",marginTop:"16px"}}>
                    Arti surat : {DataDetailBook.arti}
                  </Typography>
                  <Typography variant="body1" sx={{color:"#006064"}}>
                    Jumblah Ayat : {DataDetailBook.jumlahAyat}
                  </Typography>
                  </div>
                  <div style={{display:"flex",flexDirection:"column",alignItems:"center"}}>
                  <Typography variant="body1" component={'span'}>Putar Surat</Typography>
                  <audio controls>
                    <source src={DataDetailBook.audioFull['01']}/>
                  </audio>
                  </div>
                </div>
                <div className="contentDetailBook" style={{padding:"1rem"}}>
                <Typography variant="h6">
                  " {DataDetailBook.deskripsi} "
                </Typography>
                </div>
               
              </div>


              <div className="readBook" style={{padding:"10px 1rem 10px 1rem",display:"flex",justifyContent:"space-around"}}>
                <Button sx={{color:"#006064",opacity:'0.9'}}  onClick={()=>HandlingRead(DataDetailBook.nomor)}>Baca Surat {DataDetailBook.namaLatin}</Button>
                <Button sx={{color:"#006064"}} onClick={()=>dispatch(GETAPI_READBOOK(DataDetailBook.suratSelanjutnya.nomor))}>Surat {DataDetailBook.suratSelanjutnya.namaLatin}</Button>

              </div>

            </Container>
          </>
        ) : null}
      </div>
    </>
  );
}

export default Detailbook;
