import CardMain from "./Components/Card/Card";
import "./App.css";
import Navigation from "./Components/Navigation/Navigation";
import NavigationHead from "./Components/Navigation/NavigationHead";
import Context from "./ContextHooks/Context";
import { useEffect, useRef, useState } from "react";
import Footer from "./Components/Footer/Footer";
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';

function App() {


  const [showDetailBook,setDetailBook] = useState(false)
  const [id,setId] = useState()
  const [showBook,setShowBook] = useState(true)
  const [bottomIcon,setBotomIcon] = useState(true)
  const [titleList,setTitleList] = useState(true)
  const [darkMode,setDarkMode] = useState(true)
  const bottomRef = useRef()
  const topRef = useRef()
  
  
  function handleRef () {
    const scrollGet = document.body.getBoundingClientRect().top
  if(scrollGet < document.body.clientHeight / 50) {
    bottomRef.current.scrollIntoView()
    setBotomIcon(false)
  }
  if(scrollGet < -document.body.clientHeight / 100){
    topRef.current.scrollIntoView()
    setBotomIcon(true)
  }
  }

    useEffect(()=>{
      if(!darkMode) {
        document.body.style.backgroundColor="#263238"
        document.querySelector('.titleHead h2').style.backgroundColor = "transparent"
        document.querySelector('.titleHead h2').style.color = "#fff"
        document.querySelector('.titleHead span').style.color = '#aaa'
      } 
      if(darkMode) {
        document.body.style.backgroundColor="inherit"
   document.querySelector('.titleHead h2').style.backgroundColor = "transparent"
   document.querySelector('.titleHead h2').style.color = "inherit"
   document.querySelector('.titleHead span').style.color = '#006064'

 }
    },[darkMode])
  


 
  
  

  return (
    <>
      <Context.Provider value={{
        
        showDetailBook,setDetailBook,id,setId,showBook,setShowBook,titleList,setTitleList,darkMode,setDarkMode}}>
              
        <div className="btnBottom" onClick={()=> handleRef()}>
        <KeyboardDoubleArrowDownIcon className={bottomIcon ? 'iconArrow' : 'iconArrowTop'  }/>
        </div>
        <NavigationHead topRef={topRef}/>
        <Navigation />
        <CardMain />
        <Footer bottomRef={bottomRef}/>
      </Context.Provider>
    </>
  );
}

export default App;
