import React, { useContext } from 'react'
import ListIcon from '@mui/icons-material/List';
import "./Navigation.css"
import SearchIcon from '@mui/icons-material/Search';
import { Typography } from '@mui/material';
import Context from '../../ContextHooks/Context';
import FormControlLabel from '@mui/material/FormControlLabel';


function NavigationHead({topRef}) {
  const Contexts = useContext(Context)
  const {darkMode,setDarkMode,setTitleList} = Contexts

  function inits(){
   const ListDetail = document.querySelector('#nav')
   ListDetail.classList.toggle('navShow')
    setTitleList(true)
  
  } 

  function handleDark () {
    if(darkMode === true) {
      setDarkMode(false)
    } else {
      setDarkMode(true)
    }
  }

 
  

  return (
    <>

      <div id="navHead" ref={topRef}>
        <div className='inputSearch'>
        <div>
    <label class="switch"  >
      <input type="checkbox" onClick={handleDark}/>           
      <span class="slider"></span>
      <p class="off">No</p>
      <p class="on">Si</p>
    </label>
  </div>
      <input className='areaInput' type='text' placeholder='Cari Surat ...'/>
        </div>
        <div className='navButtonShow' style={{display:"inline-flex",alignItems:"center",gap:'2px',cursor:'pointer'}} onClick={inits} >
        <ListIcon sx={{transform:"translateY(-1px)"}}/> <Typography variant='body1'>Detail Surat</Typography>
        </div>
      </div>

    </>
  )
}

export default NavigationHead
