import { GET_API, GET_API_READBOOK } from "../Actions/Action/Action";


const initialState = {
    GET_API_RESULT : false,
    GET_API_LOADING : false,
    GET_API_ERROR : false,

    GET_API_READBOOK_RESULT : false,
    GET_API_READBOOK_LOADING : false,
    GET_API_READBOOK_ERROR : false
}

export const Reducers = (state = initialState,action) => {
    switch(action.type) {
        case GET_API : 
        return {
            ...state,
            GET_API_RESULT : action.payload.data,
            GET_API_LOADING : action.payload.loading,
            GET_API_ERROR : action.payload.error
        }
        case GET_API_READBOOK : 
        return {
            ...state,
            GET_API_READBOOK_RESULT : action.payload.data,
            GET_API_READBOOK_LOADING : action.payload.loading,
            GET_API_READBOOK_ERROR : action.payload.error
        }

        default : return state
    }
}

