import axios from 'axios'

export const GET_API = "GET_API"
export const GET_API_READBOOK = "GET_API_READBOOK"

const GETAPI = () =>  {
    return (dispatch) => {

        dispatch ({
            type : GET_API, 
            payload : {
                data : false,loading:true,timeout : 12000
            }
        })

        axios({
            url : "https://equran.id/api/v2/surat", method:"GET",UserAgent:' insomnia/2022.6.0'
        }).then((response) => {
            dispatch({
                type : GET_API,payload : {data : response,loading:false,error :false}
            })
        }).catch((error) => {
            dispatch({
                type : GET_API,payload : {data : false,loading:false,error :error}
            })
        })
       
    }


}


const GETAPI_READBOOK = (nomer) =>  {
    return (dispatch) => {

        dispatch ({
            type : GET_API_READBOOK, 
            payload : {
                data : false,loading:true,timeout : 12000
            }
        })

        axios({
            url : `https://equran.id/api/v2/surat/${nomer}`, method:"GET",UserAgent:' insomnia/2022.6.0'
            
        }).then((response) => {
            dispatch({
                type : GET_API_READBOOK,payload : {data : response,loading:false,error :false}
            })
        }).catch((error) => {
            dispatch({
                type : GET_API_READBOOK,payload : {data : false,loading:false,error :error}
            })
        })
       
    }

    
}

export {GETAPI,GETAPI_READBOOK}