import { Typography } from "@mui/material";
import React from "react";
import { useSelector } from "react-redux";
import "./Navigation.css";
import FirstPageIcon from "@mui/icons-material/FirstPage";
import ImportContactsIcon from '@mui/icons-material/ImportContacts';
import { useContext } from "react";
import Context from "../../ContextHooks/Context";
import Detailbook from "../ReadBook/DetailBook/Detailbook";

function Navigation() {
  const Selector = useSelector((state) => state.Reducers);
  const { GET_API_RESULT } = Selector;

  const Contexts = useContext(Context)
  const {
          showDetailBook,setDetailBook,
          setId,titleList,setTitleList} = Contexts

  function handleCloseNav () {
    const ListDetail = document.querySelector('#nav')

    ListDetail.classList.add('navHidden')
    ListDetail.classList.remove('navShow')

  }

function HandleButtonList(nomor) {
  setDetailBook(true)
  setId(nomor)
  setTitleList(true)
}


  
  return (
    <>
      <div id="nav" className="navHidden">
        <div className="titleNav">
          <FirstPageIcon className="btnCloseNav" onClick={()=>handleCloseNav()} />
          <Typography
            variant="h5"
            component={"h5"}
            sx={{ textAlign: "left", padding: ".5rem" }}
          >
            {titleList ? 'Detail Surat' : 'Play Surat' }
          </Typography>
          <div></div>
        </div>
        <div className="listDetail">
          {GET_API_RESULT &&
            GET_API_RESULT.data.data.map((m) => {
              return (
                <li key={m.nomor} sx={{ padding: ".6rem" }} onClick={()=>HandleButtonList(m.nomor)}>
                 <ImportContactsIcon className="ImportContactsIcon" sx={{transform:"scale(.7) translateY(10px)",color:"#212121"}}/> <Typography variant="subtitle1" component={"span"}>
                    {m.namaLatin}
                  </Typography>
                </li>
              );
            })}
        </div>
      </div>
      {showDetailBook ? <Detailbook/> : null}

    </>

    
  );

}

export default Navigation;
