import { Typography } from '@mui/material'
import React from 'react'


function ErrorHandling({GET_API_ERROR_code,GET_API_ERROR_message}) {

  


  return (
    <>
    <div id='ErrorHandling'>
    <Typography variant='title' className='textError'>{GET_API_ERROR_code}</Typography>
    <Typography variant='h5' className='textError'>{GET_API_ERROR_message}</Typography>
    </div>
    </>
  )
}

export default ErrorHandling
