import {  Typography } from "@mui/material";
import React, { useContext } from "react";
import FacebookIcon from '@mui/icons-material/Facebook';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import GitHubIcon from '@mui/icons-material/GitHub';
import './footer.css'
import Context from "../../ContextHooks/Context";

export default function Footer({bottomRef}) {
    const Contexts = useContext(Context)
  const {
         setTitleList} = Contexts

    
    function menuMusic(){
        const ListDetail = document.querySelector('#nav')
        ListDetail.classList.toggle('navShow')
        setTitleList(false)
       
       } 
    return (
        <>
            <footer ref={bottomRef}>
                <div className="footerMain"> <div>
                    <Typography variant="h5" component={'h5'}>About the web</Typography>
                    <ul className="listfooterAbout">
                        <li><Typography variant="body2" component={'span'}>Home</Typography></li>
                        <li><Typography variant="body2" component={'span'}>About us</Typography></li>
                        <li><Typography variant="body2" component={'span'}>FAQ</Typography></li>
                        <li><Typography variant="body2" component={'span'}>Contact us</Typography></li>
                        <li><Typography variant="body2" component={'span'}>API by Equran.id</Typography></li>
                    </ul>
                </div>
                <div>
                    <Typography variant="h5" component={'h5'}>Play audio webQuran</Typography>
                    <Typography> untuk mendengarkan audio web quran</Typography><Typography component={'span'} style={{color:"#eee",cursor:"pointer"}} onClick={()=>menuMusic()}>Klik disini</Typography>
                </div>
                <div>
                    <Typography variant="h5" component={'h5'}>Get in Touch</Typography>
                    <div className="ListIconFooter">
                        <FacebookIcon className="iconFooter"/> <WhatsAppIcon className="iconFooter"/> <GitHubIcon className="iconFooter"/>
                    </div>
                </div>
                </div>
               <div style={{textAlign:'center',marginTop:"2rem",display:'flex',gap:"16px",justifyContent:"center"}} className="terms">
                <Typography component={'span'}>Terms of purchase</Typography> <Typography component={'span'}>Security and privacy</Typography> <Typography component={'span'}>Newslatter</Typography>
               </div>
            </footer>
        </>
    )
}