import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import { Button, CardActionArea, CardActions } from "@mui/material";
import "./Card.css";
import { useDispatch, useSelector } from "react-redux";
import { GETAPI, GETAPI_READBOOK } from "../../REDUX/Actions";
import { useEffect } from "react";
import Loading from "../Loading/Loading";
import ErrorHandling from "./ErrorHandling";
import { useState } from "react";
import ReadBook from "../ReadBook/ReadBook";
import { useContext } from "react";
import Context from "../../ContextHooks/Context";

export default function CardMain() {
  const Selector = useSelector((state) => state.Reducers);
  // eslint-disable-next-line
  const { GET_API_RESULT, GET_API_LOADING, GET_API_ERROR,
  } = Selector;

  const Contexts = useContext(Context)
  const {showBook,setShowBook} = Contexts


  const DataExtract = GET_API_RESULT.data
    ? GET_API_RESULT.data.data.map((m) => {
        return m;
      })
    : "LoadingGETDATAEXTRACT";


  const dispatch = useDispatch();
  useEffect(
    () => {
      dispatch(GETAPI());
    }, // eslint-disable-next-line
    []
  );

 const [BookNumber,setBookNumber] = useState()

 useEffect(()=> {
      
         if(!showBook) {
          if(BookNumber){
            dispatch(GETAPI_READBOOK(BookNumber))
          }
         
     }
     
 },[showBook,BookNumber])



 const HandlingRead = (data_nomor) => {
  setShowBook(false)
  setBookNumber(data_nomor)
}

   
  return (
    <>
    <div className="titleHead" style={{display:'flex',justifyContent:"center"}}>
     <Typography className="titleHeadH2" variant="h2" gutterBottom style={{textAlign:"center",backgroundColor:"#F2F2F2",padding:'.7rem 2rem',borderRadius:"1px",display:"inline-block"}}>Web <span style={{color:"#006064"}}>Quran</span></Typography>
     </div>
      <div className="GridMenu">
        {GET_API_ERROR ? <ErrorHandling GET_API_ERROR_code={ GET_API_ERROR.code} GET_API_ERROR_message = {GET_API_ERROR.message}/>  : GET_API_LOADING ? <Loading/> : DataExtract !== "LoadingGETDATAEXTRACT"
          ? DataExtract.map((data) => {
              return (
                <Card sx={{ maxWidth: 375 }} id="Card" key={data.nomor} onClick={()=> HandlingRead(data.nomor) }>
                  <CardActionArea>
                    <CardContent >
                      <Typography gutterBottom variant="h5" component="div" color={'#212121'} >
                        {data.nomor}. {data.namaLatin} ( {data.nama} )<hr/>
                      </Typography>
                    </CardContent>
                  </CardActionArea>
                  <CardActions sx={{display:"flex",justifyContent:"space-between",marginTop:"1rem"}}>
                    <Button onClick={()=> HandlingRead(data.nomor) } size="small" sx={{color:"#006064"}}>
                      Baca
                    </Button>
                    <div id="audioplay" >
                 
                    </div>
                  </CardActions>
                </Card>

              );

            })

          : null} 

{!showBook ? <ReadBook setShowBook_prop={setShowBook} showBook_prop={showBook} nomor={BookNumber}/>  :  null }

      </div>
    </>
  );
}
