import { Button, Typography } from "@mui/material";
import React from "react";
import "./ReadBook.css";
import BackspaceIcon from "@mui/icons-material/Backspace";
import { useSelector } from "react-redux";
import Loading from "../Loading/Loading";
import ErrorHandling from "../Card/ErrorHandling";
import { useState } from "react";

function ReadBook({ setShowBook_prop }) {
  const Selector = useSelector((state) => state.Reducers);
  const {
    GET_API_READBOOK_RESULT,
    GET_API_READBOOK_LOADING,
    GET_API_READBOOK_ERROR,
  } = Selector;

  const ayat = GET_API_READBOOK_RESULT.data
    ? GET_API_READBOOK_RESULT.data.data.ayat
    : null;

  const dataAPI = GET_API_READBOOK_RESULT.data
    ? GET_API_READBOOK_RESULT.data.data
    : null;

      document.addEventListener('keydown',function(e){
        if(e.key === "Escape"){
          setShowBook_prop(true)
        }
       })
    
       const [showLatin,setShowLatin] = useState(false)



  return (
    <>  
      <div id="ReadBook">
      <Button style={{color:"#006064"}} onClick={() => setShowLatin(true)}>Baca Latin</Button>

        <div className="CloseIcon">
        <BackspaceIcon
          className="CloseIconImage"
          onClick={() => setShowBook_prop(true)}
          style={{ cursor: "pointer" }}
        /> <br/> <span className="textExit">
          ESC (exit)
        </span>
        </div>
       
        {dataAPI && (
          <>
            <Typography variant="h4" sx={{ textAlign: "center" }}>
              {dataAPI.namaLatin}
              <Typography
                variant="body2"
                component={"span"}
                style={{ display: "block" }}
              >
                Jumblah Ayat : {dataAPI.jumlahAyat}
              </Typography>
            </Typography>
          </>
        )}

        {GET_API_READBOOK_ERROR ? (
          <ErrorHandling
            GET_API_ERROR_code={GET_API_READBOOK_ERROR.code}
            GET_API_ERROR_message={GET_API_READBOOK_ERROR.message}
          />
        ) : GET_API_READBOOK_LOADING ? (
          <Loading />
        ) : (
          ayat &&
          ayat.map((m) => {
            return (
              <Typography
                component={"div"}
                key={m.nomorAyat}
                sx={{position:'relative'}}
              >

                <Typography
                  component={"p"}
                  variant={"h4"}
                  sx={{
                    boxShadow: 2,
                    display: "flex",
                    justifyContent: "flex-end",
                    alignItems: "flex-end",
                  }}
                >
                <span style={{width:'80%'}}>   <span style={{color:"#006064"}}>
                 {m.teksArab} 
                  </span> <br/> <span style={{fontSize:"16px",display:'block',  
}}>
                  {showLatin &&  m.teksLatin }
                    </span></span>
              <Typography
                    variant={"span"}
                    style={{fontSize:"1.5rem", color: "#37474f",position:"absolute",top:'.5rem',left:"1rem",textAlign:'right' }}
                    className="nomorAyat"
                  >
                    
                    {m.nomorAyat}
                  </Typography>
               

                </Typography>
                <Typography component={"span"}>
                  Artinya : <br/>" {m.teksIndonesia} "
                </Typography>
              </Typography>
            );
          })
        )}
      </div>
    </>
  );
}

export default ReadBook;
